/** ===== Notifications Handler ===== */
import {toast} from 'react-toastify';


export const ErrorNotify = error => {

    let error_message = '';

    if (error.response && error.response.status === 422)
        error_message = Object.values(error.response.data.errors)[0][0];
    else if (error.response && error.response.status === 500)
        error_message = ' .    .';
    else
        error_message = '    .'

    toast.error(error_message, MessageOptions);
}


export const SuccessNotify = (message = '    .') => {
    toast.success(message, MessageOptions);
}

export const gotennisNotif = (type = 1 ,text) => {
    let message=customeAlert('    ');
    switch(type) {
        case 1:
             message=customeAlert('    ');
            break;
        case 2:
            message=customeAlert('       ');
            break;
        case 3:
            message=customeAlert('   ');
            break;
        case 4:
            message=customeAlert(text);
            break;
        default:
        // code block
    }
     toast(message, MessageOptions);
}




const MessageOptions = {
    position: "top-right",
    autoClose: 6000,
    hideProgressBar: true,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    // limit:1
    // newestOnTop:true

}
const Style={
    display:"flex",
    fontSize:"16px",
    justifyContent: 'space-around',
    alignItems:"center",
    fontFamily:"sans"
}




const customeAlert =(text)=>{
    return(
        <div dir='rtl'  style={Style}><img src="/assets/img/logo_new.png" alt="" width={30}/> {text}  </div>
    )
}


